@import 'reset';
@import 'mixins';
@import "pages/a-index";
.categories-top{
  background: #d9d9d9;
  &__grid{
    display: grid;
    grid-template-columns: 160px calc(100% - 170px);
    gap: 10px;
  }
  &__title{
    min-height: 55px;
    text-align: right;
    display: flex;
    align-items: center;
    font-size: 14px;
    line-height: 120%;
    color: #222;
  }
}

.categories-slider{
  overflow: hidden;
  position: relative;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: 20%;
      height: auto;
    }
    &-button{
      width: 10px;
      height: 22px;
      pointer-events: unset !important;
      transform: translateY(-50%);
      margin: 0;
      &:before{
        cursor: pointer;
        content: '';
        @include centerAbsolute;
        width: 200%;
        height: 100%;
      }
      svg{
        width: 100%;
      }
    }
  }
  a{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    padding: 14px 15px 10px 15px;
    line-height: 1.22;
    font-size: 12px;
    min-height: 55px;
    text-decoration: none;
    text-transform: uppercase;
    text-align: center;
    color: #153A6B;
    font-family: "Gilroy Regular";
    &.active{
      background: #153A6B;
      color: #fff;
    }
  }
}


@media screen and (max-width: 1100px){
  .categories-slider{
    .swiper-slide{
      width: 25%;
    }
  }
}


@media screen and (max-width: 992px){
  .categories-slider{
    .swiper-slide{
      width: calc(100% / 3);
    }
  }
}


@media screen and (max-width: 650px){
  .categories-top {
    .container{
      padding: 0;
    }
    &__grid {
      grid-template-columns: 100%;
    }
    &__title{
      text-align: center;
      min-height: unset;
      padding: 10px 10px 0;
      display: block;
    }
  }
}


@media screen and (max-width: 576px){
  .categories-top{
    overflow: hidden;
  }

  .categories-slider{
    overflow: unset;
    .swiper-button{
      display: none;
    }
    .swiper-slide{
      width: 150px;
    }
  }
}
